import React from 'react';
import Fade from 'react-reveal/Fade';
export default class FactCard extends React.Component {
    constructor(props){
        super(props)
        this.firstColor = '#'+Math.random().toString(16).substr(2,6)+"30"
        this.secondColor = '#'+Math.random().toString(16).substr(2,6)+"30"
        this.left = Math.random()>.5
    }
    state={
        yPos:Math.random()*10-5,
        showSource:false,
    }
    componentDidMount(){
        this.upBob = Math.random()>.5
        setInterval(()=>{
            if(this.state.yPos>5)
                this.upBob = false
            else if(this.state.yPos<-5)
                this.upBob = true
            let changeFactor = (1-Math.pow(Math.abs(this.state.yPos)/5,2))*.1+.001
            this.setState({yPos:this.upBob?this.state.yPos+changeFactor:this.state.yPos-changeFactor})
        },10)
    }
    render(){
        return(
            <Fade left={this.left} right={!this.left}>
                <div>
                    <div style={{
                        backgroundImage:"linear-gradient(to bottom right, "+this.firstColor+", "+this.secondColor+")", 
                        borderRadius:5, 
                        borderColor:"grey", 
                        borderStyle:"solid", 
                        borderWidth:2, 
                        padding:5, 
                        marginTop:10, 
                        marginBottom:10, 
                        width:"100%",
                        boxShadow: (5-this.state.yPos)/2+"px "+(5-this.state.yPos)+"px lightgrey",
                        transition: "transform 100ms linear",
                        transform: "translateY("+this.state.yPos+"px) translateX("+this.state.yPos/2+"px)"
                    }} ref={this.absPosDiv}>
                        <p style={{margin:0}}>{this.props.text}</p>
                        {!this.state.showSource && <p style={{textAlign:"right",padding:0,margin:0, animation: "fadeIn ease 10s"}}><small onClick={()=>this.setState({showSource:true})}>View Source</small></p>}
                        {this.state.showSource && <small style={{animation: "fadeIn ease 2s"}}>{this.props.src}</small>}
                    </div>
                </div>
            </Fade>
        )
    }
}